@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@400;700&display=swap');

body {
  font-family: 'Raleway', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Raleway', sans-serif;
}

button {
  font-family: 'Raleway', sans-serif;
}

p,
a,
span,
li {
  font-family: 'Raleway', sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


.ant-modal-header {
  background-color: white !important;
}

.ant-modal .ant-modal-close {
  color: #989898 !important;
}

.ant-modal-content {
  background-color: white !important;

}

.dark-mode .ant-modal-header {
  background-color: #2A3E48 !important;
}

.dark-mode .ant-modal .ant-modal-close {
  color: black !important;
}

.dark-mode .ant-modal-content {
  background-color: #2A3E48 !important;
}



.dark-bg3 {
  background-color: #1C252A !important;
}

.dark-activeBg1 {
  background-color: #1C252A !important;
}

.dark-bg1 {
  background-color: #2A3E48 !important;
}

.dark-bg2 {
  background-color: #2A3E48 !important;
}


.dark-border1 {
  border-bottom: 1px solid #afafaf;
}

.dark-border2 {
  border: 1px solid #4c4c4c;
}

.dark-border3 {
  border-bottom: 1px solid #4c4c4c;
}




.dark-font1 {
  color: white !important;
}

.dark-color1 {
  color: #616161 !important;
}

.dark-font2 {
  color: #989898 !important;
}

.anticon-eye {
  font-size: 16px;
  color: blue;
  padding: 0.5rem;
  border-radius: 20%;
}

.anticon-eye:hover {
  background-color: rgba(0, 76, 255, 0.714);
  color: white
}
.anticon-edit {
  font-size: 16px;
  color: blue;
  padding: 0.5rem;
  border-radius: 20%;
}

.anticon-edit:hover {
  background-color: rgba(0, 76, 255, 0.714);
  color: white
}

.anticon-delete {
  font-size: 16px;
  color: red;
  padding: 0.5rem;
  border-radius: 20%;
}

.anticon-delete:hover {
  background-color: rgba(255, 0, 0, 0.714);
  color: white
}




@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}